	export default {
		data() {
			return {
				isCancel: false,
                downVideo: [],
                playFlag: true,
                name: '',
                num: 0,
                isSelect: false,
                allSelect: '全选',
                selectedId: 0,
                selectedArr: []
			}
		},
		methods: {
            creatView(){















            },
            listenerEvent(view){
            	view.setTouchEventRect({top:'0',left:'0',width:'87px',height:'44px'});
            	view.addEventListener("click", function(e){
            		console.log("点击原生控件："+JSON.stringify(e));
            	}, false);
            },
            creatVideoPlay(src){















            },
            videoMore(e){
                if (!this.isCancel) {




















































                }
            },
            polling(i){
























            },
            checkboxChange(e){
                let downItems = this.downVideo,
                    values = e.detail.value;
                this.num = values.length;
                this.selectedArr = values;
                for (var i = 0, lenI = downItems.length; i < lenI; ++i) {
                    const downItem = downItems[i];
                    const downName = downItem.name;
                    if(values.includes(downName)){
                        this.$set(downItem,'checked',true)
                    }else{
                        this.$set(downItem,'checked',false)
                    }
                }
                if (this.num < downItems.length) {
                    this.allSelect = '全选';
                } else {
                    this.allSelect = '取消全选';
                }
            },
            all_select(){
                let downItems = this.downVideo
                if (this.isSelect) {
                    if (this.num < this.downVideo.length) {
                        this.num = this.downVideo.length;
                        this.selectedArr = [];
                        for (var i = 0, lenI = downItems.length; i < lenI; ++i) {
                            const downItem = downItems[i]
                            this.$set(downItem,'checked',true)
                            this.selectedArr.push(downItem.name)
                        }
                        this.allSelect = '取消全选';
                        return
                    }
                    for (var i = 0, lenI = downItems.length; i < lenI; ++i) {
                        const downItem = downItems[i]
                        this.$set(downItem,'checked',false)
                    }
                    this.num = 0;
                    this.isSelect = false;
                    this.allSelect = '全选';
                    this.selectedArr = [];
                } else {
                    this.selectedArr = [];
                    for (var i = 0, lenI = downItems.length; i < lenI; ++i) {
                        const downItem = downItems[i]
                        this.$set(downItem,'checked',true)
                        this.selectedArr.push(downItem.name)
                    }
                    this.num = downItems.length;
                    this.isSelect = true;
                    this.allSelect = '取消全选';
                }
            },
            deleteItem(){
                let systemName = plus.os.name.toUpperCase();
                let videoArr = [this.name];
                videoArr = JSON.stringify(videoArr);
                let videoIndex = JSON.stringify(this.selectedArr);
                switch ( systemName ){
                    case "ANDROID":
                        let deleteFun = plus.android.newObject('com.tjyz.video.download.VideoUtils');
                        let percent = plus.android.invoke(deleteFun, 'deleteFile', videoArr, videoIndex);
                        var video = plus.android.newObject('com.tjyz.video.download.VideoUtils');
                        var videoList = plus.android.invoke(video,'downloadList',this.name);
                        this.downVideo = JSON.parse(videoList);








                        break;
                    case "IOS":
                        /* uni.navigateTo({
                            url: '/pages/download/videoPlay/videoPlay',
                            animationType: 'slide-in-right'
                        }); */
                        break;
                    default:
                        break;
                }
            }
		},
        onNavigationBarButtonTap() {


































        },
        onLoad(option) {
            let that = this;
            this.name = option.name;
            uni.setNavigationBarTitle({
            	title: this.name
            });






















        },
        onUnload() {
            clearInterval(this.timer);
        },
        onHide() {
            clearInterval(this.timer);
        }
	}
